import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";


class App extends React.Component{
  state = {
    isLoading : true, 
    movies:[]
  };
  getMovies = async () => {
    const 
    {data:
    {data:
    {movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by=rating");
    this.setState({movies, isLoading:false})
    // this.setState({movies:movies}) :은 ==와 같고 state의 movie(첫번째)에 axios의 movie(두번째) 넣는다! 근데 짧게해서 그냥 movies
    
  };
  componentDidMount(){
    this.getMovies();}

  render(){
    const {isLoading, movies} = this.state; //staste로 부터 isLoading이랑 movies 가져와야함~ 
    return (
      /*
      상위 React.Component class와 헷갈릴 수 있으므로 class=>className 으로 변경
      하지만 웹상의 console에는 class로 뜰 것이다
      */ 
      <section className="container">
        {isLoading
          ? <div className="loader">
              <span className = "loader_text">Loading...</span>
            </div>
          :(
            <div className="movies">
              {
                movies.map(movie => (
                  <Movie
                    key={movie.id} //key값을 주어야 unique 해져!
                    id={movie.id}
                    year={movie.year}
                    title={movie.title}
                    summary={movie.summary}
                    poster={movie.medium_cover_image}
                    genres={movie.genres}/>
                ))
              }
            </div>
          )
            
          }
      </section>

    );
    
  }




}

export default App;

